		},
		register() {
			console.log('跳转注册');
			this.phoneData = '';
			this.passData = '';
			this.$u.route({
				url: 'pages/login/register',
				animationDuration: 200
			});
		},
		forget() {
			console.log('跳转忘记密码');
			this.phoneData = '';
			this.passData = '';
			this.$u.route({
